@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap');

* {
  font-family: "Inter";
}

body {
  max-width: 600px;
  margin: 0 auto;
}

h1 {
  font-size: 45px;
  color: darkslategray;
  font-weight: 700;
}

p {
  color: dimgrey;
  line-height: 1.5;
}

button {
  padding: 12px 17px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  background-color: darkslategray;
  border: none;
  border-radius: 7px;
  margin-top: 15px;
  cursor: pointer;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: 15px;
  border-bottom: 1px solid #ccc;
  margin-top: 20px;
  border-radius: 7px;
}

.links {
  display: flex;
  gap: 20px;
}

.logo {
  color: darkslategray;
}

a {
  text-decoration: none;
  color: #3f3f3f;
}

a:hover {
  text-decoration: underline;
}

pre {
  font-family: "Jetbrains Mono";
  background-color: rgb(211, 211, 211);
  width: fit-content;
  padding: 7px 10px;
  border-radius: 7px;
  border: 2px solid #aaa;
}
